import React, { useState, useEffect } from "react";
import { db } from "../firebase/firebaseConfig";
import { collection, addDoc, deleteDoc, doc, getDocs } from "firebase/firestore";
import './AdminPage.css'; //  CSS

function AdminPage() {
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [imageUrl, setImageUrl] = useState("");  //  URL 
  const [products, setProducts] = useState([]);  //  Firestore

  // 
  const handleAddProduct = async () => {
    if (!name || !price || !imageUrl) {
      alert("");
      return;
    }

    try {
      await addDoc(collection(db, "products"), {
        name,
        price: parseFloat(price),
        imageUrl,
      });

      alert("!");
      setName("");
      setPrice("");
      setImageUrl("");
      fetchProducts();  // 
    } catch (error) {
      console.error("Error adding product:", error);
      alert("");
    }
  };

  // 
  const handleDeleteProduct = async (productId) => {
    try {
      await deleteDoc(doc(db, "products", productId));
      alert("");
      fetchProducts();  // 
    } catch (error) {
      console.error("Error deleting product:", error);
      alert("");
    }
  };

  //  Firestore
  const fetchProducts = async () => {
    const querySnapshot = await getDocs(collection(db, "products"));
    const productsArray = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
    setProducts(productsArray);  //  state 
  };

  // 
  useEffect(() => {
    fetchProducts();
  }, []);

  return (
    <div className="admin-container">
      <h2></h2>
      <div className="input-group">
        <input
          type="text"
          placeholder=""
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        <input
          type="number"
          placeholder=""
          value={price}
          onChange={(e) => setPrice(e.target.value)}
        />
        <input
          type="text"
          placeholder="URL "
          value={imageUrl}
          onChange={(e) => setImageUrl(e.target.value)}
        />
      </div>
      <button className="add-product-btn" onClick={handleAddProduct}></button>

      <h3></h3>
      <div className="product-list">
        {products.map((product) => (
          <div key={product.id} className="product-item">
            <img src={product.imageUrl} alt={product.name} />
            <div className="product-info">
              <h4>{product.name}</h4>
              <p>{product.price} </p>
            </div>
            <div className="action-buttons">
              <button
                className="edit-btn"
                onClick={() => alert("")}
              >
                
              </button>
              <button
                className="delete-btn"
                onClick={() => handleDeleteProduct(product.id)}
              >
                
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default AdminPage;